import React from 'react';
import QS from 'querystring';
import get from 'lodash/get';

import guest from '../../data/guest.json';
import Styles from './style.module.css';

function Sayhay({ location }) {
  const search = location?.search?.replace('?', '') || '';
  const { id } = QS.parse(search);
  const username = get(guest, id, 'semuanya');

  return (
    <div className={Styles['wrapper']}>
      Assalamualaikum <label className={Styles['user']}>{username}</label>, Semoga Kamu dan Keluarga dalam keadaan sehat ya.
      <br/>
      Tanpa mengurangi rasa hormat, Kami mengundang Kamu dan Partner untuk hadir di acara resepsi pernikahan Kami. 
    </div>
  )
}

export default Sayhay;